@import url(https://fonts.googleapis.com/css?family=Press+Start+2P);

$mobile-length: 660px;
$button-size: 30px;

@mixin step1( $width, $color ) {
	box-shadow:
	$width 0 $color,
	-$width 0 $color,
	0 (-$width) $color,
	0 $width $color;
	margin: 2*$width auto;
}

